@import "https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Source+Sans+Pro:wght@400;700&family=Merriweather:wght@400;700&family=Crimson+Text:wght@400;700&display=swap";
body {
  color: #0000;
  background-color: #d1b899;
  width: 100%;
  overflow-x: hidden;
}

h1 {
  color: #362004;
  justify-content: center;
  padding-bottom: 1rem;
  font-family: Crimson, serif;
  font-size: 65px;
  font-weight: bold;
  display: flex;
}

h2 {
  color: #533612;
  justify-content: center;
  align-items: center;
  padding-left: 1rem;
  padding-right: 1rem;
  font-family: Merriweather, serif;
  font-size: 25px;
  font-weight: 600;
}

.home-div p {
  color: #000;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  font-family: Source Sans Pro, sans-serif;
  font-size: 18px;
}

main {
  background-color: #d1b899;
  width: 100%;
  max-width: 1200px;
  height: auto;
  margin: auto;
  padding: 1rem;
}

.home-div {
  background-color: #d1b899;
  gap: 1rem;
  max-width: 1200px;
  height: auto;
  margin: auto;
  padding: 1rem 2rem;
  display: flex;
}

.article {
  transition: all .3 ease;
  flex-direction: column;
  align-content: baseline;
  align-items: center;
  height: auto;
  display: grid;
  position: relative;
}

.article:hover .article-photo {
  filter: none;
  border-radius: 25px;
  transition: all .3s;
}

.article:hover {
  background-color: #c29e6e;
  border-radius: 20px;
  transition: all .3s;
  transform: translateY(.5rem);
}

#top-stories {
  transition: all .3 ease;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  width: 70%;
  height: auto;
  display: grid;
}

#sidebar {
  color: #fff;
  background-color: #362004;
  flex-direction: column;
  grid-template-columns: 1fr;
  justify-content: center;
  width: 30%;
  height: auto;
  display: flex;
}

.sidebar-item {
  transition: all .3 ease;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  position: relative;
}

.sb-text a {
  color: #d1b899;
  font-size: 18px;
}

#sidebar p {
  color: #fff;
  font-size: 15px;
}

.sidebar-photo {
  object-fit: cover;
  filter: grayscale(75%);
  justify-content: center;
  width: 100%;
  height: 200px;
  padding-bottom: 1rem;
  display: flex;
}

.sidebar-item:hover .sidebar-photo {
  filter: none;
  border-radius: 25px;
  transition: all .3s;
}

.article-photo {
  object-fit: cover;
  filter: grayscale(75%);
  align-items: center;
  width: 100%;
  height: 250px;
  padding: 1rem;
}

@media (width <= 768px) {
  #home-div {
    padding: 1rem;
  }

  h1 {
    font-size: 40px;
  }

  h2 {
    padding: .5rem;
    font-size: 20px;
  }

  .home-div {
    grid-template-columns: 1fr;
  }

  .home-div p {
    padding: .5rem;
    font-size: 16px;
  }

  #top-stories {
    grid-template-columns: 1fr;
    width: 100%;
  }

  #sidebar, .sidebar-item {
    display: none;
  }

  .article {
    padding: 1rem;
  }

  .sidebar-photo, .article-photo {
    height: 150px;
  }
}
